@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #091e18;
  --color-bg-variant: #396c2c;
  --color-primary: rgb(81, 255, 0); 
  --color-primary-variant: rgba(104, 255, 77, 0.4);
  --color-white: #fff;
  --color-light: rgb(255, 255, 255, 0.6);

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition: all 400ms ease;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Bebas Neue', cursive;
  font-size: xx-large;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.2;
  background-image: url(../src/assets/fondo.jpeg);
  background-size: cover;
  
}


.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5 {
  font-weight: 500;
}

h1 {
  font-size: 5rem;
}

section {
  margin-top: 8rem;
}

section > h2, section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
color: var(--color-primary);
margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
 
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);

}

img {
  display: block;
  width: 100%;
  object-fit: cover;
  box-shadow: 0 0 8px 8px #396c2c inset;
}

/* ---------media querie for tablets---------- */

@media screen and (max-width: 1024px) {
  .container{
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* ---------media querie for cellphone---------- */

@media screen and (max-width: 600px) {
  .container{
    width: var(--container-width-sm);
  }

  section {
    margin-bottom: 2rem;
  }
}
