nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.8rem 0.8rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 4rem;
    border-radius: 3rem;
    backdrop-filter: blur(2px);
}

nav a {
    background: transparent;
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 2.2rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0, 0.3);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}

/* ---------media querie for tablets---------- */

@media screen and (max-width: 1024px) {
    nav {
        bottom: 2rem;
        gap: 1.8rem;
    }
  }

  /* ---------media querie for cellphone---------- */
  
  @media screen and (max-width: 600px) {
    nav {
        bottom: 3rem;
        gap: 0.8rem;
    }
  }