header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.8rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1.3rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 5rem;
    background: var(--color-primary);
}

.me {
    background: linear-gradient(var(--color-primary), transparent 8rem);
    width: 22rem;
    height: 33rem;
    position: absolute;
    left: calc(50% - 11rem); 
    margin-top: 5rem;
    border-radius: 6rem 6rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.me:hover {
    transform: scale(1.4);
}

.scroll__down {
    position: absolute;
    right: -1.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1.5rem;
    color: var(--color-light);
}

/* ---------media querie for tablets---------- */

@media screen and (max-width: 1024px) {
    .header {
        height: 98vh;
    }

  }
  
  /* ---------media querie for cellphone---------- */
  
  @media screen and (max-width: 600px) {
   .header {
    height: 100vh;
   }

   .header__socials, .scroll__down {
    display: none;
   }
  }
  